import algoliasearch from 'algoliasearch/lite';
import {
    AisConfigure,
    AisCurrentRefinements,
    AisHits,
    AisInstantSearch,
    AisMenu,
    AisPagination,
    AisRefinementList,
    AisStats,
    AisSearchBox,
} from 'vue-instantsearch';

import {history as historyRouter} from 'instantsearch.js/es/lib/routers';

const routing = {
    it: {
        router: historyRouter(),
        stateMapping: {
            stateToRoute({query, refinementList, page}) {
                return {
                    query: query,
                    applications:
                        refinementList &&
                        refinementList['application.it'] &&
                        refinementList['application.it'].join('~'),
                    classifications:
                        refinementList &&
                        refinementList['commercial_classification.names'] &&
                        refinementList['commercial_classification.names'].join('~'),
                    segments:
                        refinementList &&
                        refinementList['segment.it.names'] &&
                        refinementList['segment.it.names'].join('~'),
                    base_types:
                        refinementList &&
                        refinementList['base_type.it'] &&
                        refinementList['base_type.it'].join('~'),
                    viscosities:
                        refinementList &&
                        refinementList['viscosity'] &&
                        refinementList['viscosity'].join('~'),
                    page: page
                };
            },
            routeToState({query, applications, classifications, segments, base_types, viscosities, page}) {
                return {
                    query: query,
                    refinementList: {
                        'application.it': applications && applications.split('~'),
                        'commercial_classification.names': classifications && classifications.split('~'),
                        'segment.it.names': segments && segments.split('~'),
                        'base_type.it': base_types && base_types.split('~'),
                        'viscosity': viscosities && viscosities.split('~')
                    },
                    page: page
                };
            }
        }
    },

    en: {
        router: historyRouter(),
        stateMapping: {
            stateToRoute({query, refinementList, page}) {
                return {
                    query: query,
                    applications:
                        refinementList &&
                        refinementList['application.en'] &&
                        refinementList['application.en'].join('~'),
                    classifications:
                        refinementList &&
                        refinementList['commercial_classification.names'] &&
                        refinementList['commercial_classification.names'].join('~'),
                    segments:
                        refinementList &&
                        refinementList['segment.en.names'] &&
                        refinementList['segment.en.names'].join('~'),
                    base_types:
                        refinementList &&
                        refinementList['base_type.en'] &&
                        refinementList['base_type.en'].join('~'),
                    viscosities:
                        refinementList &&
                        refinementList['viscosity'] &&
                        refinementList['viscosity'].join('~'),
                    page: page
                };
            },
            routeToState({query, applications, classifications, segments, base_types, viscosities, page}) {
                return {
                    query: query,
                    refinementList: {
                        'application.en': applications && applications.split('~'),
                        'commercial_classification.names': classifications && classifications.split('~'),
                        'segment.en.names': segments && segments.split('~'),
                        'base_type.en': base_types && base_types.split('~'),
                        'viscosity': viscosities && viscosities.split('~')
                    },
                    page: page
                };
            }
        }
    }
};

export default {

    props: ['baseUrl', 'algoliaAppId', 'algoliaSearchKey', 'refinementsOrder'],

    components: {
        AisInstantSearch,
        AisHits,
        AisConfigure,
        AisRefinementList,
        AisMenu,
        AisPagination,
        AisCurrentRefinements,
        AisStats,
        AisSearchBox,
    },

    data() {
        return {
            searchClient: algoliasearch(
                this.algoliaAppId,
                this.algoliaSearchKey
            ),
            routing: null
        };
    },

    created() {
        if (this.baseUrl) {
            // this.setBaseUrl(this.baseUrl);
        }
    },


    methods: {},

    watch: {}


};
